.App {
  display: flex;
  align-items: center;
  justify-content: center;
  /* background-color: var(--secondary); */
  height: 100%;
  color: var(--text-color);
}

.Dark {
  /* --primary: #006095; */
  --primary: #003e61;
  --primary-container: #1e1e1e;
  --secondary: #121212;
  --secondary-text: #888;
  --on-primary: #272727;
  --active-primary: #373737;
  --text-color: #d5d5d5;
  --dyn-text-color: #fff;
  --app-background: linear-gradient(109.6deg, #0e1214 11.2%, #0b1116 51.2%, rgb(3 15 26) 98.6%);
  --shadow-color: #000;
  --primary-rgb: 0, 62, 97;
  --primary-hover: #005381;
  --primary-hover-rgb: 0, 83, 129;
  --primary-active: #004164;
  --surface-color: #ffffff10;
  --on-surface: #ffffff20;
  --default: linear-gradient(120deg, #3b7493, #00345e);
  --purple: linear-gradient(120deg, #713b93, #28005e);
  --green: linear-gradient(120deg, #65933b, #005e5a);
  --green: linear-gradient(120deg, #11291d, #00312e);
  --black: linear-gradient(120deg, #202020, #080808);
  /* TODO: Add following Gradients */
  /* background: linear-gradient(135deg, black, #330000); */
  /* background: linear-gradient(135deg, black, #000033); */
  /* background: linear-gradient(45deg, black, #1a1a1a); */
  /* background: linear-gradient(45deg, white, #ffffff); */
}

:root {
  --primary: #00aeff;
  --primary-container: #f1f1f1;
  --secondary: #fff;
  --secondary-text: #aaa;
  --on-primary: #f1f1f1;
  --active-primary: #eee;
  --text-color: #000;
  --dyn-text-color: #000;
  --background: #0e1214;
  --shadow-color: #fff;
  --primary-rgb: 0, 174, 255;
  --primary-hover: #008dcf;
  --primary-hover-rgb: 0, 141, 207;
  --primary-active: #0080bb;
  --surface-color: #00000010;
  --on-surface: #00000020;
  --default: linear-gradient(120deg, #62cdff, #096bd1);
  --purple: linear-gradient(120deg, #a256d3, #4900ad);
  --green: linear-gradient(120deg, #9adf5b, #008d87);
  --black: linear-gradient(120deg, #464646, #1a1a1a);
}

:root {
  --emoji-size: 20px;
}

.textfield {
  position: relative;
  margin: 12px 0;
}

.textfield label {
  position: absolute;
  font-size: 1rem;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  background-color: var(--primary-container);
  color: gray;
  padding: 0 0.3rem;
  margin: 0 0.5rem;
  transition: 0.1s ease-out;
  transform-origin: left top;
  pointer-events: none;
}

.textfield input {
  font-size: 1rem;
  outline: none;
  height: 54px;
  border: 1px solid gray;
  border-radius: 10px;
  padding: 1rem 0.7rem;
  color: var(--dyn-text-color);
  background-color: var(--primary-container);
  width: 100%;
  font-family: inherit;
  transition: 0.1s ease-out;
}

.textfield input:hover,
.textfield input:focus {
  border-color: var(--primary);
  transition: border-width 0.15s ease-in;
}

.textfield input:focus {
  border-width: 2px;
}

.textfield input:hover+label,
.textfield input:focus+label {
  color: var(--primary);
}

.textfield.error input:hover+label,
.textfield.error input:focus+label {
  color: #dd0000;
}

.textfield input:focus+label {
  top: 0;
  transform: translateY(-50%) scale(0.9);
}

.textfield input:not(:placeholder-shown)+label {
  top: 0;
  transform: translateY(-50%) scale(0.9);
}

.textfield.disable input {
  background-color: #eeeeee20;
  color: #888;
  border-color: #88888830;
}

.textfield.disable label {
  opacity: 0;
}

.textfield.error input {
  border-color: #dd0000;
}

.textfield.error label {
  color: #dd0000;
}

#alert-dialog-title {
  display: flex;
  align-items: center;
  gap: 12px;
}

.background {
  background: radial-gradient(rgb(12, 20, 69) -16.9%, rgb(71, 30, 84) 119.9%);
  /* filter: blur(10px); */
  background: linear-gradient(109.6deg, rgb(11 16 24) 11.2%, rgb(11 23 35) 51.2%, rgb(3 15 26) 98.6%);
  background: linear-gradient(109.6deg, #f8fcff 11.2%, #f4faff 51.2%, rgb(249 252 255) 98.6%);
  width: 100%;
  position: absolute;
  height: 100%;
  z-index: -1;
}

.background {
  background: var(--app-background);
}

.danger {
  color: #dd0000 !important;
}

.Home {
  display: flex;
  flex: 1 1;
  height: calc(100% - 48px);
  margin: 24px;
  /* background-color: #00000010; */
  border: 1px solid #00000010;
  border-radius: 16px;
  overflow: hidden;
}

.Dark .Home {
  /* background-color: var(--surface-color); */
  border: 1px solid var(--surface-color);
}

.LeftColumn {
  flex: 1;
  max-width: 420px;
  border-right: 1px solid var(--surface-color);
  border-radius: 16px;
  position: relative;
  overflow: hidden;
  background-color: var(--background);
  z-index: 1;
}

.MiddleColumn {
  flex: 3 1;
  display: flex;
  flex-direction: column;
  position: relative;
  top: 0;
  left: 0;
}

.UserDetails {
  display: flex;
  flex-direction: column;
  height: 2.75rem;
  justify-content: space-evenly;
}

.UserDetails .subtitle {
  font-size: 14px;
}

@media screen and (max-width: 925px) {
  .LeftColumn {
    position: fixed;
    left: 0;
    top: 0;
    /* height: calc(var(--vh, 1vh)*100); */
    height: 100%;
    width: 26.5rem !important;
    /* transform: translate3d(-5rem, 0, 0); */
    transition: transform var(--layer-transition);
  }

  .MiddleColumn {
    max-width: none;
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    transform: translate3d(26.5rem, 0, 0);
    z-index: 1;
    transition: all .3s ease;
  }

  .MiddleColumn .background {
    margin-left: 0 !important;
    padding-left: 0 !important;
  }

  .MiddleColumn.focused {
    transform: none;
  }

  .MiddleColumn .ChatInfo .BackArrow {
    display: block;
  }
}

.icon {
  padding: 6px;
  border-radius: 8px;
  margin: 0 8px;
  cursor: pointer;
  user-select: none;
  transition: all .3s ease;
}

.icon:hover {
  background-color: var(--surface-color);
}

.icon:active {
  background-color: var(--on-surface);
}

.profile {
  width: 3.175rem;
  height: 3.175rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--primary);
  color: #fff;
  border-radius: 50%;
  font-size: 24px;
}

.scrollable {
  overflow-y: overlay;
  scroll-behavior: smooth;
}

@-moz-document url-prefix() {
  .scrollable {
    /* Firefix supports */
    scrollbar-width: thin;
    scrollbar-color: rgba(0, 0, 0, 0.2) #0000;
  }
}

.scrollable::-webkit-scrollbar {
  width: 7px !important;
}

.scrollable::-webkit-scrollbar-thumb {
  border-radius: 10px !important;
  -webkit-box-shadow: inset 0 0 6px rgb(0 0 0 / 10%) !important;
  background-color: rgb(0 0 0 / 10%) !important;
}

.scrollable::-webkit-scrollbar-thumb:hover {
  background-color: rgb(0 0 0 / 30%) !important;
}

.scrollable::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgb(0 0 0 / 0%) !important;
  border-radius: 10px !important;
}

.scrollable::-webkit-scrollbar {
  width: 7px !important;
}

.scrollable::-webkit-scrollbar-thumb {
  border-radius: 10px !important;
  -webkit-box-shadow: inset 0 0 6px rgb(0 0 0 / 10%) !important;
  background-color: rgb(0 0 0 / 10%) !important;
}

.scrollable::-webkit-scrollbar-thumb:hover {
  background-color: rgb(0 0 0 / 30%) !important;
}

.scrollable::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgb(0 0 0 / 0%) !important;
  border-radius: 10px !important;
}

.Menu {
  position: relative;
  transition: transform .3s ease;
}

.Menu.active {
  background-color: var(--on-surface);
}

.Menu>.icon {
  position: relative;
}

.DropdownMenu,
.ContextMenu {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  background-color: var(--surface-color);
  position: absolute;
  min-width: 13.5rem;
  border-radius: 10px;
  z-index: 4;
  margin: 4px;
  gap: 4px;
  padding: 4px;
  -webkit-backdrop-filter: blur(2.5rem);
  backdrop-filter: blur(2.5rem);
  user-select: none;
  transform-origin: center !important;
  overflow: hidden;
  box-shadow: 0px 0px 10px -4px #000000aa;
  transition: transform 0.2s ease, opacity 0.2s ease, width .2s ease, height .2s ease;
}

.ContextMenu {
  min-width: 10rem;
}

.DropdownMenu.animate,
.ContextMenu.animate {
  /* transform: scale(0.95); */
  transform: scale(0.95) rotateX(45deg) translateY(-18px);
  opacity: 0;
}

.ContextMenu.hidden {
  display: none;
}

.MenuItem {
  display: flex;
  align-items: center;
  width: 100%;
  min-height: 34px;
  border-radius: 8px;
  padding-right: 10px;
  cursor: pointer;
  transition: all .15s ease;
}

.MenuItem.withSubtitle {
  height: 44px;
}

.MenuItem>div {
  margin-left: 8px;
}

.MenuItem>div~div {
  margin: 0;
}

.MenuItem:hover {
  background-color: var(--on-primary);
}

.MenuItem:active {
  transform: scale(.95);
}

.MenuItem .icon {
  font-size: 22px;
}

.MenuItem .title {
  font-family: Roboto, Vazirmatn;
  font-weight: bold;
}

.MenuItem .subtitle {
  font-size: 12px;
}

.DropdownMenu .top {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.DropdownMenu hr {
  margin: 0;
  width: 100%;
  border-color: #ffffff10;
}

.DropdownMenu .profile,
.ContextMenu .profile {
  margin: 2px 10px;
}

.TopBar .DropdownMenu {
  top: -8px !important;
}

.DropdownMenu.top {
  top: 0;
}

.DropdownMenu.left {
  left: 0;
}

.DropdownMenu.right {
  right: 0;
}

.DropdownMenu.bottom {
  bottom: 0;
}

.DropdownMenu.withoutTitle {
  top: 36px !important;
}

.highlight {
  background-color: rgba(var(--primary-rgb), 0.2);
}

.scrollToBottom {
  width: 42px;
  height: 42px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--surface-color);
  border-radius: 16px;
  position: absolute;
  bottom: 56px;
  right: 0;
  margin: 16px;
  backdrop-filter: blur(2.5rem);
  transition: background-color .3s ease, bottom .3s ease, opacity .3s ease;
}

.scrollToBottom.hidden {
  bottom: 0 !important;
  opacity: 0;
  z-index: -1;
}

.scrollToBottom:hover {
  background-color: var(--on-surface);
}

.scrollToBottom .icon:hover {
  background: none;
}

.Page,
.SubPage {
  width: 100%;
  height: calc(100% - 54px);
  background-color: var(--secondary);
  position: absolute;
  left: 0;
  transition: all .3s ease;
}

.SubPage {
  height: 100%;
  max-height: none;
}

.Page.scrollable,
.SubPage.scrollable {
  scrollbar-width: none;
}

.Page .Items {
  display: flex;
  flex-direction: column;
}

.Page .Items>.title {
  font-size: 16px;
  color: #666;
  padding: 4px;
  margin: 8px 20px;
}

.Page .Item {
  height: 60px;
  display: flex;
  align-items: center;
  width: 100%;
  padding-right: 16px;
  font-size: 18px;
  cursor: pointer;
  border-radius: 16px;
  border-bottom: 1px solid var(--primary-container);
  transition: all .3s ease-in-out;
}

.Page .Item .icon {
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.Page .Item>span {
  flex: 1;
}

.Page .Item>span:first-child {
  padding-left: 28px;
}

.Page .Item .profile {
  margin: 0 12px;
}

.Page .section.Info {
  border-radius: 0 0 16px 16px;
}

.Page .Tabs {
  display: flex;
  gap: 8px;
  padding: 8px;
}

.Page .Tab {
  background-color: var(--secondary);
  padding: 8px;
  border-radius: 8px;
}

.Page .Tab.active {
  background-color: var(--primary-container);
}

.Page.animate {
  height: 120px;
  opacity: 0;
  transform: scale(.95);
  display: block;
}

.Page .pushUp,
.SubPage .pushUp {
  transform: translateY(-50px);
}

.Page .User {
  display: flex;
  align-items: center;
  padding: 16px 0;
  gap: 12px;
  font-size: 20px;
  flex-direction: row;
  border-bottom: 1px solid var(--primary-container);
  background-color: var(--secondary);
  margin: 0 24px;
}

.StartChat {
  padding: 8px;
  background-color: var(--primary-container);
  width: 54px;
  height: 54px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 16px;
  position: absolute;
  right: 0;
  margin: 0 24px;
  margin-bottom: 0px;
  margin-bottom: -80px;
}

.SubPage {
  top: 0;
}

.SubPage.animate {
  transform: translateY(100%);
  opacity: 0;
  display: block;
}

.Page>div,
.SubPage>div {
  transition: all .3s ease;
}

.Page .fadeThrough {
  opacity: 0;
  transform: translateY(20px);
}

.emoji-inner {
  display: inline-block;
  width: 20px;
  height: 20px;
  vertical-align: middle;
}

.emoji {
  background: no-repeat;
  background-size: var(--emoji-size);
  color: rgba(0, 0, 0, 0);
  display: inline-block;
  width: var(--emoji-size);
  height: var(--emoji-size);
  margin-inline-end: 1px;
  vertical-align: middle;
  overflow: hidden;
  flex-shrink: 0;
}

.Transition {
  display: contents;
}

.Page .section {
  background-color: var(--secondary);
}

.Page .textfield {
  flex: 1;
}

.Page .textfield input {
  border: none;
  font-size: 18px;
  height: 48px;
}

.fab {
  position: absolute;
  bottom: 0;
  right: 0;
  margin: 16px;
  width: 3.375rem;
  height: 3.375rem;
  border-radius: 16px;
  background-color: var(--primary-container);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all .3s ease;
  z-index: 5;
}

.fab::after {
  content: '';
  width: 100%;
  height: 100%;
  border-radius: 16px;
  position: absolute;
  z-index: -1;
  transition: all .3s ease;
}

.fab:hover::after {
  background-color: var(--on-primary);
}

.fab .icon {
  background: none !important;
}

.switch {
  position: relative;
  display: inline-block;
  width: 54px;
  height: 28px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.switch .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: 100%;
  background-color: var(--primary-container);
  border-radius: 34px;
  -webkit-transition: .4s;
  transition: .4s;
}

.switch .slider:before {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  border-radius: 50%;
  -webkit-transition: .4s;
  transition: .4s;
}

.switch input:checked+.slider {
  background-color: var(--primary);
}

.switch input:focus+.slider {
  box-shadow: 0 0 3px var(--primary);
}

.switch input:checked+.slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}