@media screen and (max-width: 480px) {
    .Home {
        margin: 0;
        height: 100%;
        border-radius: 0;
    }

    .LeftColumn {
        width: 100% !important;
        border-radius: 0;
        z-index: 0;
    }

    .MiddleColumn {
        display: none;
    }

    .MiddleColumn.active {
        display: flex;
    }

    .MiddleColumn {
        position: fixed;
        width: 100%;
        height: 100%;
    }

    .MiddleColumn .background {
        display: block !important;
    }

    .MiddleColumn .background::after {
        content: '';
        position: fixed;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: #ffffff10;
    }

    .MiddleColumn .ChatInfo .BackArrow {
        display: block;
    }

    .Home .ContextMenuBG {
        opacity: unset;
    }

    .Home .MenuItem {
        padding: 2px 0;
    }

    .message-media {
        width: calc(60vw + .575rem * 2) !important;
    }
}