.Auth.container {
    background-color: var(--secondary);
    display: flex;
    /* background-color: #ffffff08; */
    backdrop-filter: blur(50px);
    width: -moz-fit-content;
    width: fit-content;
    padding: 20px;
    border-radius: 16px;
    transition: all .3s ease-in-out;
    max-width: 420px;
    width: 100%;
}

.Auth.container>div {
    flex: 1;
}

.Auth.container button {
    appearance: none;
    border: none;
    outline: none;
    border-radius: 10px;
    background-color: var(--primary);
    text-align: center;
    padding: 16px;
    color: #fff;
    font-family: inherit;
    text-transform: uppercase;
    font-size: 20px;
    width: 100%;
    transition: all .3s ease;
    cursor: pointer;
}

.Auth.container .title {
    text-align: center;
    font-size: 24px;
    margin: 10px 0;
}

.Auth.container p {
    font-size: 14px;
    color: #606060;
    margin: 1.5em 0;
    padding: 0 12px;
    line-height: 1.5rem;
}

.Auth.container .textfield input {
    background-color: transparent;
}

.Auth.container .textfield label {
    background-color: var(--secondary);
}