.Message {
    padding: 0 8px;
    width: 100%;
    display: flex;
    transition: background-color .3s ease;
    /* transition: all .3s ease; */
}

.Message.animate {
    transform: scale(0.9);
}

.Message.Out {
    flex-direction: row-reverse;
}

.Message.In {
    flex-direction: row;
}

.Message .message-from-profile {
    align-self: flex-end;
    margin-right: 8px;
    min-width: 36px;
}

.Message .message-from-profile.hidden .profile {
    display: none;
}

.Message .bubble {
    background-color: var(--surface-color);
    /* padding: 8px; */
    padding: 0.575rem;
    margin: 4px 0;
    width: -moz-fit-content;
    width: fit-content;
    max-width: 70%;
    height: -moz-fit-content;
    height: fit-content;
    border-radius: 16px;
    display: flex;
    overflow: hidden;
}

.Message .body {
    display: flex;
    flex-direction: column;
}

.Message .body .from {
    color: var(--primary);
}

.Message .message-text {
    display: inline-block;
    word-break: break-word;
    line-height: 1.2;
}

.Message .message-details {
    float: right;
    align-self: flex-end;
    align-items: center;
    padding-top: 7px;
    padding-left: 7px;
    margin-bottom: -7px;
    font-size: 12px;
    color: #888;
    direction: ltr;
    display: flex;
    gap: 2px;
    user-select: none;
}

.Message .message-details .message-time {
    font-size: 12px;
    color: #888;
}

.message-details .message-seen {
    font-size: 17px;
    width: auto;
    height: auto;
    color: var(--green-color);
}

.message-reply {
    font-size: 13px;
    display: flex;
    align-items: center;
    gap: 4px;
    line-height: 1.2;
    height: 36px;
    width: 100%;
    padding: 2px 0;
    border-radius: 8px;
    user-select: none;
    cursor: pointer;
    transition: all 0.3s ease;
}

.message-reply:hover {
    background-color: var(--on-primary);
}

.message-reply .line {
    background-color: var(--primary);
    width: 2px;
    height: 100%;
    border-radius: 16px;
}

.message-reply .title {
    color: var(--primary);
    font-weight: bold;
}

.Message .icon {
    margin: 0;
    padding: 0;
    font-size: 17px;
}

.message-media {
    /* max-width: 420px;
    max-height: 50vh; */
    overflow: hidden;
    margin: -0.575rem;
    margin-bottom: 0.285rem;
    position: relative;
    background-color: #aaa;
    display: flex;
    align-items: center;
    justify-content: center;
}

/* .Message.In .message-media {
    margin-top: 0.285rem;
} */

/* .message-media img {
    width: 100%;
    height: 100%;
    min-width: 12vw;
    min-height: 10vh;
} */

.message-media>* {
    transition: all .3s ease;
}

.message-media .blurred {
    filter: blur(10px);
}

.message-loading-progress {
    position: absolute;
    padding: 3px;
    width: 54px;
    height: 54px;
    background-color: #0009;
    display: flex;
    border-radius: 50%;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all .3s ease;
}

.message-loading-progress:hover {
    background-color: #000b;
}

.message-loading-progress .icon {
    position: absolute;
    background-color: transparent;
}

.message-loading-progress.animate {
    opacity: 0;
    transform: scale(.8);
}

@keyframes spinner {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}